import Icon from "@mui/material/Icon";
import Features from "layouts/sections/page-sections/featuers";
import Navbars from "layouts/sections/navigation/navbars";
import NavTabs from "layouts/sections/navigation/nav-tabs";

import SignIn from "layouts/pages/authentication/sign-in";

import SignUpBasic from "pages/LandingPages/Register";
import CompleteRegistration from "pages/LandingPages/SignIn/SignInSkills";
import PresentationPage from "layouts/pages/presentation";
import Pagination from "layouts/sections/navigation/pagination";
import SuggestionOffre from "layouts/sections/navigation/suggestion";

const routes = [
  {
    name: "A propos des offres",
    icon: <Icon>article</Icon>,
    collapse: [
      {
        name: "Acceuil",
        description: "Consultez toutes les offres disponibles",
        route: "/presentation",
        component: <PresentationPage />,
      },
      {
        name: "Liste de mes candidatures",
        description: "Consultez toutes vos candidatures dposes pour des stages ou emplois en un seul endroit.",
        route: "/offres/candidatures",
        component: <NavTabs />,
      },
      {
        name: "Liste des emplois les plus suggrer",
        description: "Dcouvrez les offres d'emploi les plus recommandes en fonction de votre profil et de vos comptences.",
        route: "/offres/suggestion",
        component: <SuggestionOffre />,
      }
    ],
  },
  {
    name: "Moi",
    icon: <Icon>person</Icon>,
    collapse: [
      {
        name: "Modification de mes comptences",
        description: "Mettez  jour vos comptences pour reflter vos dernires acquisitions et augmenter vos chances de trouver des opportunits adaptes.",
        route: "/competence/modification",
        component: <Navbars />,
      },
      {
        name: "Mes informations",
        description: "Consultez le CV complet de l'tudiant, avec ses diplmes, comptences, expriences professionnelles et langues matrises",
        route: "/profil/information",
        component: <Features />,
      }
    ],
  },
  {
    name: "",
    columns: 1,
    icon: <Icon>logout</Icon>,
    rowsPerColumn: 2,
    collapse: [
      {
        name: "account",
        collapse: [
          {
            name: "sign in",
            route: "/pages/authentication/sign-in",
            component: <SignIn />,
          },
        ],
      },
      {
        name: "Detail offre",
        collapse: [
          {
            name: "Detail offre",
            route: "/offre/:id",
            component: <Pagination />,
          },
        ]
      },
      {
        name: "Sign Up",
        collapse:[
          {
            name: "sign up",
            route: "/pages/authentication/signup",
            component: <SignUpBasic />
          }
        ]
      },
      {
        name: "Complete Registration",
        collapse:[
          {
            name: "Complete Registration",
            route: "/pages/authentication/complete-registration/:numETU/:email",
            component: <CompleteRegistration />
          }
        ]
      },
    ],
  }
];

export default routes;
